import React,{useState,useEffect} from 'react'
import {useNavigate} from 'react-router-dom'
import axios from 'axios'
import Login from '../components/Login'
import SignUp from '../components/SignUp';
import './User.css'
import {backendApi,validUser} from '../common/common'
import LogOut from '../components/LogOut';

export default function User() {
  const [signUpToggle, setSignUpToggle] = useState(false);
  const [message, setMessage] = useState("");
  const [reload, setReload] = useState("");
  const [login,setLogin] = useState(false);

  const navigate=useNavigate();

  useEffect(() => {
    if(localStorage.getItem("login")==validUser){
      setLogin(true);
      setReload("");
    }
    window.scrollTo(0, 0);
  }, [reload])  

  const handleSignUpM=(info)=>{
    if(confirm("SignUp")){
      axios
        .post(`${backendApi}/signup`,info)
        .then(res => {
          
          if(res.data.message=="SignUp Successfully"){
            setSignUpToggle(false);
            setMessage(res.data.message + " Please Login");
          }
          else{
            setMessage(res.data.message);
          }
          return res;
        })
        .catch(err => console.error(err));
    }
  }

  const handleLoginM=(info)=>{
    if(confirm("Login")){
      axios
        .post(`${backendApi}/login`,info)
        .then(res => {
          setMessage(res.data.message);
          if(res.data.message=="Login Successfully"){
            localStorage.setItem("login","valid");
            localStorage.setItem("userName",res.data.userName)
            setMessage(res.data.message);
            setReload(res.data.message);
            navigate("/")
          }
          else{
            setMessage(res.data.message);
          }
          return res;
        })
        .catch(err => console.error(err));
    }
  }

  const handleLogOut=()=>{
    localStorage.removeItem("login");
    localStorage.removeItem("userName");
    setLogin(false);
    setMessage("");
  }


  return (
    <div className='user'>
      {(login)?
        <>
          {message && <h3>{message}</h3>}
          <LogOut passHandleLogOut={handleLogOut}/>
        </>
        :
        <>
          {message && <h3>{message}</h3>}
          {!signUpToggle &&
            <>
              <Login passHandleLoginM={handleLoginM}/>
              <div className='exchange'>
                <button  onClick={()=>setSignUpToggle(true)}>New User SignUp</button>
              </div>
            </>
          }
          {signUpToggle && 
            <>
              <SignUp passHandleSignUpM={handleSignUpM}/>
              <div className='exchange'>
                <button onClick={()=>setSignUpToggle(false)}>I have SignUp</button>
              </div>
            </>
          }
        </>
      }
    </div>
  )
}
