import React,{useState,useEffect} from 'react'
import axios from 'axios'
import "./DoctorTips.css";
// import Data from "../data.json";
import DoctorCard from './DoctorCard';
import {backendApi} from '../common/common';


export default function DoctorTips(props) {
  const {predictDataPass}=props;
  
  const [doctorData, setDoctorData] = useState([]);

  useEffect(() => {
    axios
      .get(`${backendApi}/doctors`)
      .then(res => {
        setDoctorData(res.data);
        return res;
      })
      .catch(err => console.error(err));
  }, [])
  


  return (
    <div className='doctor-tips'>
      {predictDataPass && (predictDataPass.length!=0) && 
        <div>
          {predictDataPass[predictDataPass.length-1].ten<40 ? 
            <div>
              {
                doctorData && doctorData.filter(item=>item.predict=="low").map(obj=>{
                  return(
                    <div key={obj.id}>
                      <DoctorCard objPass={obj}/>
                    </div>
                  )
                })
              }
            </div>:
            <div>
              {
                predictDataPass[predictDataPass.length-1].ten<60 ?
                <div>
                  {
                    doctorData && doctorData.filter(item=>item.predict=="medium").map(obj=>{
                      return(
                        <div key={obj.id}>
                          <DoctorCard objPass={obj}/>
                        </div>
                      )
                    })
                  }
                </div>:
                <div>
                  {
                    doctorData && doctorData.filter(item=>item.predict=="high").map(obj=>{
                      return(
                        <div key={obj.id}>
                          <DoctorCard objPass={obj}/>
                        </div>
                      )
                    })
                  }
                </div>
              }
            </div>
          }
        </div>
      }
    </div>
  )

}
