import React,{useState,useEffect} from 'react'
import './PredictForm.css'
import axios from 'axios'
import TopSlider from '../components/TopSlider'
import {backendApi,validUser} from '../common/common'
import { useNavigate } from "react-router-dom"

export default function PredictForm() {
  const navigate = useNavigate();

  const [isLoading, setIsLoading] = useState(false);

  const [value0, setValue0] = useState(false);
  const [value1, setValue1] = useState(false);
  const [value2, setValue2] = useState(false);
  const [value3, setValue3] = useState(0);
  const [value4, setValue4] = useState(0);
  const [value5, setValue5] = useState(0);
  const [value6, setValue6] = useState(0);
  const [value7, setValue7] = useState(0);
  const [value8, setValue8] = useState(0);
  const [value9, setValue9] = useState(0);
  const [value10, setValue10] = useState(0);
  const [value11, setValue11] = useState(0);
  const [value12, setValue12] = useState(0);
  const [value13, setValue13] = useState(0);
  const [value14, setValue14] = useState(0);
  const [value15, setValue15] = useState(0);


  const totalValue={
    value0,
    value1,
    value2,
    value3,
    value4,
    value5,
    value6,
    value7,
    value8,
    value9,
    value10,
    value11,
    value12,
    value13,
    value14,
    value15
  }

  const handlePredict=()=>{
    if(confirm("predict")){
      setIsLoading(true);
      axios
        .post(`${backendApi}/predict`,(localStorage.getItem('login')==validUser)?
        {...totalValue,login:'true',userName:localStorage.getItem('userName')}:
        {...totalValue,login:'false',userName:null})
        .then(res =>{
          const predictSingle=[{
            ten:res.data.result.tendency,
            time:new Date().toLocaleDateString()
          }]
          localStorage.setItem("predictSingle",JSON.stringify(predictSingle));
          navigate("/analysis");
          setIsLoading(false);
          window.scrollTo(0, 0);
          return res;
        })
        .catch(err => console.error(err));
    }
  }


  return (
    <div className='predict-form-m'>
      <div className='predict-home' id='predicthome'>
        <a  href="#predict">Tendency Predict</a>
        <a  href="#">Doctor</a>
        <div className='bg-img'>
        </div>
      </div>

      <div className='predict-form' id='predict'>
        <div className='predict-slider'>
          <TopSlider/>
        </div>
        <div>
        </div>
        <div className='form'>
          <div className="range">
            <h3>Have you tried suicide before ? <br /><span>আগের আত্মহত্যার চেষ্টা করেছেন ?</span></h3>
            <div className="field">
              <input type="radio" name='check1' onChange={() => setValue0(true)}/><label style={{marginRight:'5px'}}><h3>Yes</h3></label>
              <input type="radio" name='check1' checked={!value0} onChange={() => setValue0(false)}/><label><h3>NO</h3></label>
            </div>
          </div><br />
          
          <div className="range">
            <h3>Do you get angry easily ? <br /><span>আপনি কি সহজে রেগে যান ?</span></h3>
            <div className="field">
              <input type="radio" name='check2' onChange={() => setValue1(true)}/><label style={{marginRight:'5px'}}><h3>Yes</h3></label>
              <input type="radio" name='check2' checked={!value1} onChange={() => setValue1(false)}/><label><h3>NO</h3></label>
            </div>
          </div><br />

          <div className="range">
            <h3>Is your loved one dead ? <br /><span>আপনার প্রিয়জন কি মৃত ?</span></h3>
            <div className="field">
              <input type="radio" name='check3' onChange={() => setValue2(true)}/><label style={{marginRight:'5px'}}><h3>Yes</h3></label>
              <input type="radio" name='check3' checked={!value2} onChange={() => setValue2(false)}/><label><h3>NO</h3></label>
            </div>
          </div><br />

          <div className="range">
            <h3>percentage of mental stress ?<br /><span>মানসিক চাপ কত শতাংশ?</span></h3>
            <div className="sliderValue">
              {value3>60?<span style={{color:'red'}}>{value3} %</span>:value3>40?<span style={{color:'yellow',backgroundColor:'gray'}}>{value3} %</span>:<span style={{color:'green'}}>{value3} %</span>}
            </div>
            <div className="field">
              <div className='value-left'>0</div>
              <input type="range" min="0" max="100" step="5" value={value3} onChange={(e)=>setValue3(e.target.value)}></input>
              <div className='value-right'>100</div>
            </div>
          </div><br />

          <div className="range">
            <h3>How much of a job/financial problem or loss ? <br /><span>চাকরি/আর্থিক সমস্যা বা ক্ষতি কতটা ?</span></h3>
            <div className="sliderValue">
              {value4>60?<span style={{color:'red'}}>{value4} %</span>:value4>40?<span style={{color:'yellow',backgroundColor:'gray'}}>{value4} %</span>:<span style={{color:'green'}}>{value4} %</span>}
            </div>
            <div className="field">
              <div className='value-left'>0</div>
              <input type="range" min="0" max="100" step="5" value={value4} onChange={(e)=>setValue4(e.target.value)}></input>
              <div className='value-right'>100</div>
            </div>
          </div><br />



          <div className="range">
            <h3>How much is depression and other mental illnesses ?<br /><span>বিষণ্নতা এবং অন্যান্য মানসিক রোগ কতটা ?</span></h3>
            <div className="sliderValue">
              {value5>60?<span style={{color:'red'}}>{value5} %</span>:value5>40?<span style={{color:'yellow',backgroundColor:'gray'}}>{value5} %</span>:<span style={{color:'green'}}>{value5} %</span>}
            </div>
            <div className="field">
              <div className='value-left'>0</div>
              <input type="range" min="0" max="100" step="5" value={value5} onChange={(e)=>setValue5(e.target.value)}></input>
              <div className='value-right'>100</div>
            </div>
          </div><br />

          <div className="range">
            <h3>Serious illness such as chronic pain ? <br /><span>গুরুতর অসুস্থতা যেমন দীর্ঘস্থায়ী ব্যথা ?</span></h3>
            <div className="sliderValue">
              {value6>60?<span style={{color:'red'}}>{value6} %</span>:value6>40?<span style={{color:'yellow',backgroundColor:'gray'}}>{value6} %</span>:<span style={{color:'green'}}>{value6} %</span>}
            </div>
            <div className="field">
              <div className='value-left'>0</div>
              <input type="range" min="0" max="100" step="5" value={value6} onChange={(e)=>setValue6(e.target.value)}></input>
              <div className='value-right'>100</div>
            </div>
          </div><br />

          <div className="range">
            <h3>Criminal/legal problems ?<br /><span>ফৌজদারি/আইনগত সমস্যা ?</span></h3>
            <div className="sliderValue">
              {value7>60?<span style={{color:'red'}}>{value7} %</span>:value7>40?<span style={{color:'yellow',backgroundColor:'gray'}}>{value7} %</span>:<span style={{color:'green'}}>{value7} %</span>}
            </div>
            <div className="field">
              <div className='value-left'>0</div>
              <input type="range"  min="0" max="100" step="5" value={value7} onChange={(e)=>setValue7(e.target.value)}></input>
              <div className='value-right'>100</div>
            </div>
          </div><br />



          <div className="range">
            <h3>How much emotional or aggressive tendencies ?<br /><span>আবেগপ্রবণ বা আক্রমণাত্মক প্রবণতা কতটা ?</span> </h3>
            <div className="sliderValue">
              {value8>60?<span style={{color:'red'}}>{value8} %</span>:value8>40?<span style={{color:'yellow',backgroundColor:'gray'}}>{value8} %</span>:<span style={{color:'green'}}>{value8} %</span>}
            </div>
            <div className="field">
              <div className='value-left'>0</div>
              <input type="range"  min="0" max="100" step="5" value={value8} onChange={(e)=>setValue8(e.target.value)}></input>
              <div className='value-right'>100</div>
            </div>
          </div><br />

          <div className="range">
            <h3>Loss of relationships ?<br /><span>সম্পর্কের ক্ষতি ?</span></h3>
            <div className="sliderValue">
             {value9>60?<span style={{color:'red'}}>{value9} %</span>:value9>40?<span style={{color:'yellow',backgroundColor:'gray'}}>{value9} %</span>:<span style={{color:'green'}}>{value9} %</span>}
            </div>
            <div className="field">
              <div className='value-left'>0</div>
              <input type="range" min="0" max="100" step="5" value={value9} onChange={(e)=>setValue9(e.target.value)}></input>
              <div className='value-right'>100</div>
            </div>
          </div><br />

          <div className="range">
            <h3>Victim of bullying ? <br /><span>বুলিং/ক্ষাপানো এর শিকার ?</span></h3>
            <div className="sliderValue">
              {value10>60?<span style={{color:'red'}}>{value10} %</span>:value10>40?<span style={{color:'yellow',backgroundColor:'gray'}}>{value10} %</span>:<span style={{color:'green'}}>{value10} %</span>}
            </div>
            <div className="field">
              <div className='value-left'>0</div>
              <input type="range"  min="0" max="100" step="5" value={value10} onChange={(e)=>setValue10(e.target.value)}></input>
              <div className='value-right'>100</div>
            </div>
          </div><br />

          <div className="range">
            <h3>Victim of discrimination ?<br /><span>বৈষম্যতার শিকার ?</span></h3>
            <div className="sliderValue">
              {value11>60?<span style={{color:'red'}}>{value11} %</span>:value11>40?<span style={{color:'yellow',backgroundColor:'gray'}}>{value11} %</span>:<span style={{color:'green'}}>{value11} %</span>}
            </div>
            <div className="field">
              <div className='value-left'>0</div>
              <input type="range"  min="0" max="100" step="5" value={value11} onChange={(e)=>setValue11(e.target.value)}></input>
              <div className='value-right'>100</div>
            </div>
          </div><br />
          
          <div className="range">
            <h3>A victim of blackmail ? <br /><span>ব্ল্যাকমেইল এর শিকার ?</span></h3>
            <div className="sliderValue">
              {value12>60?<span style={{color:'red'}}>{value12} %</span>:value12>40?<span style={{color:'yellow',backgroundColor:'gray'}}>{value12} %</span>:<span style={{color:'green'}}>{value12} %</span>}
            </div>
            <div className="field">
              <div className='value-left'>0</div>
              <input type="range" min="0" max="100" step="5" value={value12} onChange={(e)=>setValue12(e.target.value)}></input>
              <div className='value-right'>100</div>
            </div>
          </div><br />

          <div className="range">
            <h3>Violence victimization and perpetration ?<br /><span>সহিংসতার শিকার এবং অপরাধ ?</span></h3>
            <div className="sliderValue">
              {value13>60?<span style={{color:'red'}}>{value13} %</span>:value13>40?<span style={{color:'yellow',backgroundColor:'gray'}}>{value13} %</span>:<span style={{color:'green'}}>{value13} %</span>}

            </div>
            <div className="field">
              <div className='value-left'>0</div>
              <input type="range" min="0" max="100" step="5" value={value13} onChange={(e)=>setValue13(e.target.value)}></input>
              <div className='value-right'>100</div>
            </div>
          </div><br />

          <div className="range">
            <h3>Social isolation ?<br /><span>সামাজিক বিচ্ছিন্নতা ?</span></h3>
            <div className="sliderValue">
              {value14>60?<span style={{color:'red'}}>{value14} %</span>:value14>40?<span style={{color:'yellow',backgroundColor:'gray'}}>{value14} %</span>:<span style={{color:'green'}}>{value14} %</span>}
            </div>
            <div className="field">
              <div className='value-left'>0</div>
              <input type="range" min="0" max="100" step="5" value={value14} onChange={(e)=>setValue14(e.target.value)}></input>
              <div className='value-right'>100</div>
            </div>
          </div><br />

          <div className="range">
            <h3>Suicide clusters in communities ?<br /><span>সম্প্রদায়ের মধ্যে আত্মহত্যার ক্লাস্টার ?</span></h3>
            <div className="sliderValue">
              {value15>60?<span style={{color:'red'}}>{value15} %</span>:value15>40?<span style={{color:'yellow',backgroundColor:'gray'}}>{value15} %</span>:<span style={{color:'green'}}>{value15} %</span>}
            </div>
            <div className="field">
              <div className='value-left'>0</div>
              <input type="range" min="0" max="100" step="5" value={value15} onChange={(e)=>setValue15(e.target.value)}></input>
              <div className='value-right'>100</div>
            </div>
          </div><br />

          <div className='data-submit'>
              <input type="submit" value={isLoading ? "Loading ... ":"Predict"} onClick={handlePredict}/>
          </div><br />
        </div>
      </div>
    </div>
  )
}
