import React from 'react'
import Popup from 'reactjs-popup';
import {backendApi} from "../common/common";
import "./DoctorCard.css";

export default function DoctorCard(props) {
  const {id,name,img,t1,t2,t3,t4,predict,tips}=props.objPass;
  return (
    <div>
      <div className='doctor-card'>
        <div className='doctor'>
          <img src={img} alt="" />
          <h3>{name}</h3>
          <Popup trigger={<button style={{cursor:'pointer',margin:'2px'}}>Profile</button>} position="right bottom">
            <div style={{textAlign:'center'}}>
              <img style={{borderRadius:'.3rem'}} width={100} src={img} alt="" />
              <div style={{backgroundColor:'#eee'}}>
                <h4 style={{color:'blue',padding:'4px'}}>{name}</h4>
                <h5 style={{color:'black',padding:'3px'}}>{t1}</h5>
                <h5 style={{color:'green',padding:'3px'}}>{t2}</h5>
                <h5 style={{color:'black',padding:'3px'}}>{t3}</h5>
                <h5 style={{color:'green',padding:'3px'}}>{t4}</h5>
              </div>
            </div>
          </Popup>
        </div>
        <div>
          <p>{tips}</p>
        </div>

      </div>
    </div>
  )
}
