import React,{useState,useEffect} from 'react'
import {Link, NavLink} from 'react-router-dom'
import './Analysis.css'
import axios from 'axios';
import {backendApi,validUser} from '../common/common'
import { LineChart, Line, CartesianGrid, XAxis, YAxis,Tooltip } from 'recharts';
import DoctorTips from '../components/DoctorTips';

export default function Analysis() {

  const [predictData, setPredictData] = useState([]);
  useEffect(() => {
    axios
      .get(`${backendApi}/analysis/${localStorage.getItem("userName")}`)
      .then(res => {
        setPredictData(JSON.parse(res.data.result).filter(item=>item.ten!="0"));
        return res;
      })
      .catch(err => console.error(err));

    window.scrollTo(0, 0);
  }, [])
  

  if(localStorage.getItem("login")==validUser){
    return (
      <div className='analysis'>
        {predictData &&
          <div>
            <div className='chart'>
              <p style={{margin:'15px 25px'}}>Y-100</p>
              <LineChart width={500} height={300} data={predictData}>
                <Line type="monotone" dataKey="ten" stroke="#0075FF" />
                <CartesianGrid stroke="#ccc" />
                <XAxis dataKey="time" />
                <YAxis />
                <Tooltip />
              </LineChart>  
            </div>
            <DoctorTips predictDataPass={predictData}/>
          </div>
        }
      </div>
    )
  }

  else{
    return(
      <div className='analysis'>
        <div style={{textAlign:'center',color:'tomato',padding:'5px'}}>
          <Link to="/user"><h3>Please Login</h3></Link>
        </div>
        {localStorage.getItem("predictSingle") &&
          <div>
            <div className='chart'>
              <p style={{margin:'15px 25px'}}>Y-100</p>
              <LineChart width={500} height={300} data={JSON.parse(localStorage.getItem("predictSingle"))}>
                <Line type="monotone" dataKey="ten" stroke="#0075FF" />
                <CartesianGrid stroke="#ccc" />
                <XAxis dataKey="time" />
                <YAxis />
                <Tooltip />
              </LineChart>
            </div>
            <DoctorTips predictDataPass={JSON.parse(localStorage.getItem("predictSingle"))}/>
          </div>
        }
      </div>
    )
  }
}
