import React from 'react'
import { Link, NavLink ,Navigate,useNavigate} from 'react-router-dom'
import './Navbar.css'
import {BsPersonLinesFill} from "react-icons/bs";
import icon from '../images/predictive.png'



export default function navbar() {

  const navigate = useNavigate()

  const handleReload=(e)=>{
    e.preventDefault();
    navigate("/");
    window.scrollTo(0, 0);
  }
  return (
    <div className='navbar'>
      <div className='nav1'>
        {/* <a href="#predicthome">sss</a> */}
        <button style={{border:'none',cursor: 'pointer',borderRadius:'.3rem',background:'none'}} onClick={handleReload}><img width={20} src={icon} alt="icon" /></button>
        <NavLink to="/">SuicidePredict</NavLink>
        <NavLink to="/analysis">Analysis</NavLink>
      </div>
      <div className='nav2'>
        <NavLink to="/user"><BsPersonLinesFill/></NavLink>
      </div>
    </div>
  )
}
