import React from 'react'
import Slider from 'react-slick'
import "./TopSlider.css";

export default function TopSlider() {

  const settings = {
    infinite: true,
    speed: 1000,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 3000,
    cssEase: "linear"
  };

  return (
    <div className='top-slider'>
      <Slider {...settings}>
        <div>
          <h3 style={{color:'#3498db'}}>Suicidal Tendency Prediction</h3>
        </div>
        <div>
          <h3 style={{color:'#444444'}}>Suicidal Tendency Prediction</h3>
        </div>
        <div>
          <h3 style={{color:'#0075FF'}}>Suicidal Tendency Prediction</h3>
        </div>
      </Slider>
    </div>
  )
}
